import React from 'react';
import "./Navbar.css";
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import LoginIcon from '@mui/icons-material/Login';
import HowToRegIcon from '@mui/icons-material/HowToReg';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import fakeImg from "../../image/fake.png";
import { Link } from "react-router-dom";
// import { useSelector } from 'react-redux';

const Navbar = () => {
  // const state = useSelector((state) => state.handleCart);
  return (
    <>

<nav className="navbar navbar-expand-lg bg-white navbar-light py-2 shadow-sm">
  <div className="container">
    <Link className="navbar-brand fw-bold fs-3" to="/">
       <img src={fakeImg} alt="logo image" className='fakeImg' />
    </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/About"> About </Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/product"> Product </Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/Contact"> Contact </Link>
        </li>
      </ul>
      
      <div className="buttons">
        <Link to="/Login" className="btn btn-outline-dark me-1"> <LoginIcon id="choto" /> Login </Link>
        <Link to="/Register" className="btn btn-outline-dark ms-2"> <HowToRegIcon id="choto" /> Register </Link>
        <Link to="/Cart" className="btn btn-outline-dark ms-2"> <AddShoppingCartIcon id="choto" /> Cart(0)</Link>
      </div>
    </div>
  </div>
</nav>

    </>
  )
}

export default Navbar;
